.App {
  /* text-align: center; */
  background-color: #282c34;
  padding: 40px;
  font-size: 14px;
  display: flex;

  .content-left {
    width: 20%;
    padding: 10px;
  }
  .content-right {
    width: 80%;
    padding: 10px;
    color: white;
  }
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* min-height: 100vh; */
  /* display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.input-product {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-bottom: 10px;
}
.input-product input {
  width: 60%;
  height: 24px;
  outline: none;
  border-radius: 3px;
  border: none;
}
button {
  cursor: pointer;
}
